.types {
  width: 100%;
  padding: 149px 25px 147px;

  background: $color-background-dark-grey;

  @media (max-width: $tablet-width) {
    padding: 50px 15px 20px;
  }
}

.types__title {
  margin: 0 0 51px;

  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  text-align: center;
  color: $color-special-black;

  @media (max-width: $tablet-width) {
    margin: 0 0 30px;

    font-size: 24px;
    line-height: 41px;
  }
}

.types__list {
  position: relative;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 1176px;
  margin: 0 auto;
  padding: 0;

  list-style: none;
}

.types__item {
  position: relative;

  margin: 0 25px 20px;

  @media (max-width: $tablet-width) {
    margin-bottom: 50px;
  }

  &:first-child {
    z-index: 1;

    margin-top: 10px;

    &::before {
      content: "";
      position: absolute;
      top: 127px;
      left: -60px;
      z-index: -1;

      width: 180px;
      height: 180px;

      background-image: url("../img/svg/ellipse.svg");
      background-size: contain;

      @media (max-width: $tablet-width) {
        top: 200px;
        left: 115px;

        width: 127px;
        height: 127px;
      }

      @media (max-width: $mobile-width) {
        top: 200px;
        left: 0;
      }
    }

    @media (max-width: $tablet-width) {
      margin-top: 0;
    }
  }

  &:nth-child(2) {
    margin-top: 39px;

    &::before {
      content: "";
      position: absolute;
      top: -18px;
      left: -19px;

      width: 47px;
      height: 47px;

      background-image: url("../img/svg/rectangle.svg");
      background-size: contain;

      @media (max-width: $tablet-width) {
        display: none;
      }
    }

    &::after {
      content: "";
      position: absolute;
      right: -23px;
      bottom: 43px;

      width: 47px;
      height: 47px;

      background-image: url("../img/svg/rectangle.svg");
      background-size: contain;

      @media (max-width: $tablet-width) {
        display: none;
      }
    }
  }

  &:last-child {
    &::after {
      content: "";
      position: absolute;
      top: -71px;
      right: -36px;

      width: 95px;
      height: 95px;

      background-image: url("../img/svg/ellipse.svg");
      background-size: contain;

      @media (max-width: $tablet-width) {
        display: none;
      }
    }

    @media (max-width: $tablet-width) {
      margin-top: 0;

      &::before {
        top: -20px;
        right: -25px;

        width: 68px;
        height: 68px;
        margin-top: 0;
      }
    }
  }
}

.types__link {
  &:hover {
    color: $color-turquoise;
  }
}

.types__picture {
  width: 100%;

  img {
    border-radius: 10px;

    &:hover {
      box-shadow: 0 0 5px 1px $color-turquoise;
    }

    @media (max-width: $tablet-width) {
      width: 270px;
      height: 300px;
    }
  }
}

.types__description {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  padding: 5px 10px;

  font-weight: 600;
  font-size: 22px;
  line-height: 34px;
}
